/* font */
@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/variable/pretendardvariable.css');

/* XEICON CDN */
@import url("//cdn.jsdelivr.net/npm/xeicon@2.3.3/xeicon.min.css");

/* default CSS */
* {
  box-sizing: border-box;
  letter-spacing: -1px;
  font-family: 'Pretendard Variable';
}
body {
  line-height: 1em;
  font-weight: 400;
  font-size: 15px;
  color: #121212;
}
a {
  color: #121212;
  text-decoration: none;
}
img {
  object-fit: cover;
}

/* scrollbar */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background-color: transparent;
}
::-webkit-scrollbar-thumb {
  border-radius: 40px;
  background-color: rgba(0,0,0,0.15);
}

/* Reset CSS */
body, ul, ol, h1, h2, h3, h4, h5, h6, p, button {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
input, select, textarea, button {
  display: inline-block;
  border: none;
  background-color: transparent;
  outline: none;
  font-size: 15px;
}

/* ### Common CSS ### */
.header, .footer {
  height: 60px;
}

/* ### header ### */
.header {
  line-height: 60px;
  text-align: center;
  box-shadow: 0 6px 10px rgba(0,0,0,0.02);
}
.headerLogo {
  font-weight: 700;
  font-size: 20px;
}

/* trigger */
.header {
  position: relative;
}
.trigger {
  position: absolute;
  width: 20px;
  height: 13px;
  left: 14px;
  top: 50%;
  transform: translateY(-50%);
}
.trigger span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: rgb(44, 147, 250);
  border-radius: 20px;
}
.trigger span:nth-child(1) {
  top: 0;
}
.trigger span:nth-child(2) {
  top: 50%;
}
.trigger span:nth-child(3) {
  top: 100%;
}
.headerLogo {
  font-weight: 700;
}

/* sidebar */

/* ### content ### */
.content {
  height: calc(100vh - calc(60px * 2));
  overflow-y: scroll;
}

/* ### footer ### */
.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #dedede;
  text-align: center;
}
.footer div {
  flex: 1;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer div:not(div:last-child) {
  border-right: 1px solid #dedede;
}
.footer img {
  width: 28px;
  object-fit: cover;
}

/* ### To Do List ### */
.todolistTitle {
  margin: 0 0 35px;
  font-weight: 700;
  font-size: 42px;
}
.todolistInput {
  min-width: 250px;
  height: 45px;
  padding-left: 10px;
  vertical-align: top;
  border: 1px solid #dedede;
  border-right: none;
}
.todolistBtn {
  width: 45px;
  height: 45px;
  background-color: dodgerblue;
  border: 1px solid dodgerblue;
  color: #fff;
  font-size: 22px;
}
.todolistItems {
  margin: 30px 0 0;
}
.todolistItems li {
  margin: 17px 0 0;
  padding: 16px 0 16px 14px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0,0,0,0.05);
}
.todolistItems input[name="todolistChk"] {}
.todolistItems label {
  display: inline-block;
  margin-left: 4px;
  vertical-align: middle;
  transform: translateY(-2px);
  font-weight: 600;
}

input[name="todolistChk"]:checked + label {
  text-decoration: line-through;
  font-weight: 500;
  color: #ccc;
}

/* Responsive */
@media (min-width: 768px) {
  .todolistWrap {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (max-width: 767.98px) {
  /* ### content ### */
  .content {
    padding: 45px 14px 30px;
  }

  /* ### To Do List ### */
  .todolistTitle {
    margin: 0 0 23px;
    font-size: 32px;
  }
}